export class IndexedDBHelper {
    dbName;
    storeName;
    db = null;
    msgRejectNotStarted = "Database not initialized.";
    msgRejectNotOpenDb = "Error opening database:";
    msgItemAdded = "Item added successfully";
    msgNotItemAdded = "Error adding item:";
    msgNotGet = "Error getting:";
    msgRemoveItem = "Item removed successfully";
    msgNotRemoveItem = "Error removing item:";
    msgRemoveAll = "All removed successfully";
    msgNotRemoveAll = "Error removing all:";
    constructor(dbName, storeName) {
        this.dbName = dbName;
        this.storeName = storeName;
    }
    openDB() {
        return new Promise((resolve, reject) => {
            const request = indexedDB.open(this.dbName, 1);
            request.onupgradeneeded = (event) => {
                const db = event.target.result;
                if (!db.objectStoreNames.contains(this.storeName)) {
                    db.createObjectStore(this.storeName, {
                        keyPath: "id",
                        autoIncrement: true,
                    });
                }
            };
            request.onsuccess = (event) => {
                this.db = event.target.result;
                resolve(this.db);
            };
            request.onerror = (event) => {
                reject(`${this.msgRejectNotOpenDb} ${event.target.error}`);
            };
        });
    }
    addItem(data) {
        return new Promise((resolve, reject) => {
            if (!this.db) {
                reject(this.msgRejectNotStarted);
                return;
            }
            const transaction = this.db.transaction(this.storeName, "readwrite");
            const store = transaction.objectStore(this.storeName);
            const request = store.add(data);
            request.onsuccess = () => {
                resolve(this.msgItemAdded);
            };
            request.onerror = (event) => {
                reject(`${this.msgNotItemAdded}: ${event.target.error}`);
            };
        });
    }
    getAllItems() {
        return new Promise((resolve, reject) => {
            if (!this.db) {
                reject(this.msgRejectNotStarted);
                return;
            }
            const transaction = this.db.transaction(this.storeName, "readonly");
            const store = transaction.objectStore(this.storeName);
            const request = store.getAll();
            request.onsuccess = (event) => {
                resolve(event.target.result);
            };
            request.onerror = (event) => {
                reject(`${this.msgNotGet} ${event.target.error}`);
            };
        });
    }
    getItemById(id) {
        return new Promise((resolve, reject) => {
            if (!this.db) {
                reject(this.msgRejectNotStarted);
                return;
            }
            const transaction = this.db.transaction(this.storeName, "readonly");
            const store = transaction.objectStore(this.storeName);
            const request = store.get(id);
            request.onsuccess = (event) => {
                resolve(event.target.result);
            };
            request.onerror = (event) => {
                reject(`${this.msgNotGet} ${event.target.error}`);
            };
        });
    }
    removeItemById(id) {
        return new Promise((resolve, reject) => {
            if (!this.db) {
                reject(this.msgRejectNotStarted);
                return;
            }
            const transaction = this.db.transaction(this.storeName, "readwrite");
            const store = transaction.objectStore(this.storeName);
            const request = store.delete(id);
            request.onsuccess = () => {
                resolve(this.msgRemoveItem);
            };
            request.onerror = (event) => {
                reject(`${this.msgNotRemoveItem} ${event.target.error}`);
            };
        });
    }
    removeAllItems() {
        return new Promise((resolve, reject) => {
            if (!this.db) {
                reject(this.msgRejectNotStarted);
                return;
            }
            const transaction = this.db.transaction(this.storeName, "readwrite");
            const store = transaction.objectStore(this.storeName);
            const request = store.clear();
            request.onsuccess = () => {
                resolve(this.msgRemoveAll);
            };
            request.onerror = (event) => {
                reject(`${this.msgNotRemoveAll} ${event.target.error}`);
            };
        });
    }
}
