export const capitalizeTextWithSpaces = (text) => {
    return text.toLowerCase().replace(/(?:^|\s)\S/g, (a) => {
        return a.toUpperCase();
    });
};
export const formatCPF = (text) => {
    text = String(text).replace(/\D/g, "");
    text = text.slice(0, 11);
    return text.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/, "$1.$2.$3-$4");
};
export const formatCNPJ = (text) => {
    text = String(text).replace(/\D/g, "");
    text = text.slice(0, 14);
    return text.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/, "$1.$2.$3/$4-$5");
};
export const formatTelephone = (text) => {
    text = String(text).replace(/\D/g, "");
    text = text.slice(0, 11);
    return text.replace(/(\d{2})(\d{5})(\d{4})/, "($1) $2-$3");
};
export const formatNumber = (text) => {
    const filter = +text.replace(/[^0-9]/g, "");
    return filter;
};
export const formatCEP = (text) => {
    let numericText = text.replace(/[^0-9]/g, "");
    numericText = numericText.slice(0, 8);
    return numericText.replace(/(\d{5})(\d{3})/, "$1-$2");
};
export const formatByExample = (value, example) => {
    const specialCharactersRegex = /[^\w]/;
    let formattedValue = "";
    let valueIndex = 0;
    for (let i = 0; i < example.length; i++) {
        const exampleChar = example[i];
        if (exampleChar.match(specialCharactersRegex)) {
            formattedValue += exampleChar;
        }
        else if (valueIndex < value.length) {
            formattedValue += value[valueIndex];
            valueIndex++;
        }
        else {
            break;
        }
    }
    return formattedValue;
};
export const findModifiedValuesObject = (oldData, data) => {
    const newData = {};
    Object.keys(oldData).forEach((key, index) => {
        if (JSON.stringify(data[key]) !== JSON.stringify(oldData[key])) {
            newData[key] = data[key];
        }
    });
    return newData;
};
export const formatMoney = (value, currency, locale, tag = false) => {
    const numericValue = typeof value === "string" ? parseFloat(value) : typeof value === "number" ? value : 0;
    const formattedValue = new Intl.NumberFormat(locale, {
        style: "currency",
        currency: currency,
    }).format(numericValue);
    const formattedWithoutSymbol = formattedValue
        .replace(/[^0-9.,]/g, "")
        .replace(",", ".")
        .replace("$", "")
        .trim();
    return tag ? formattedValue : formattedWithoutSymbol;
};
export const limitTextSpread = (max, text) => {
    if (text.length > max) {
        return `${text.slice(0, max)}...`;
    }
    return text;
};
export const limitText = (max, text) => {
    if (text.length > max) {
        return `${text.slice(0, max)}`;
    }
    return text;
};
export const setUrlSearchParam = (object) => {
    const url = new URL(window.location);
    Object.keys(object).forEach((key) => {
        const value = JSON.stringify(object[key]);
        if (key === "page" && value === "1") {
            return null;
        }
        if (value !== "" && value !== null && value !== "null") {
            url.searchParams.set(key, object[key]);
        }
    });
    window.history.pushState({}, "", url);
};
export const resetUrlSearchParam = (object) => {
    const url = new URL(window.location.href);
    const params = new URLSearchParams();
    Object.keys(object).forEach((key) => {
        const value = JSON.stringify(object[key]);
        if (value !== "" && value !== null && value !== "null") {
            params.set(key, object[key]);
        }
    });
    const newUrl = `${url.origin + url.pathname}?${params.toString()}`;
    window.history.pushState({}, "", newUrl);
};
export const getParamsInQs = () => {
    const qs = queryString.parse(window.location.search);
    if (Object.keys(qs).length > 0) {
        return {
            data: qs,
            r: true,
        };
    }
    return {
        r: false,
    };
};
export const moveElement = (array, ofIndex, toIndex) => {
    const indexPosition = ofIndex < 0 || ofIndex >= array.length || toIndex < 0 || toIndex >= array.length;
    if (!indexPosition) {
        const [movedElement] = array.splice(ofIndex, 1);
        array.splice(toIndex, 0, movedElement);
        return array;
    }
    return array;
};
