export const fromNow = (lastDate, abbreviatedText, timesText) => {
    const seconds = (new Date().getTime() - new Date(lastDate).getTime()) / 1000;
    const timesTextValidation = {
        second: abbreviatedText
            ? "s"
            : seconds >= 2
                ? ` ${timesText?.second.pluralText.toLowerCase().trim() ?? "seconds"}`
                : ` ${timesText?.second.text.toLowerCase().trim() ?? "second"}`,
        minute: abbreviatedText
            ? "m"
            : seconds >= 120
                ? ` ${timesText?.minute.pluralText.toLowerCase().trim() ?? "minutes"}`
                : ` ${timesText?.minute.text.toLowerCase().trim() ?? "minute"}`,
        hour: abbreviatedText
            ? "h"
            : seconds >= 7200
                ? ` ${timesText?.hour.pluralText.toLowerCase().trim() ?? "hours"}`
                : ` ${timesText?.hour.text.toLowerCase().trim() ?? "hour"}`,
    };
    if (seconds < 0)
        return "0 seconds";
    if (seconds <= 59) {
        return `${Math.round(seconds)}${timesTextValidation.second}`;
    }
    if (seconds <= 3599) {
        const minutes = Math.round(seconds / 60);
        return `${minutes}${timesTextValidation.minute}`;
    }
    if (seconds >= 3600) {
        const hours = Math.round(seconds / 3600);
        return `${hours}${timesTextValidation.hour}`;
    }
    return seconds.toString();
};
export const exportTableXLSX = (params) => {
    const { headers = [], rows = [], widthOfColumns = [], tableName = "table" } = params;
    const data = [headers];
    rows.forEach((object) => {
        const newArray = [];
        Object.values(object).forEach((element) => newArray.push(element));
        data.push(newArray);
    });
    const wscols = widthOfColumns.map((widthOfColum) => ({ wch: widthOfColum }));
    const worksheet = XLSX.utils.aoa_to_sheet(data);
    worksheet["!cols"] = wscols;
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, tableName);
    const wbout = XLSX.write(workbook, { bookType: "xlsx", type: "array" });
    const blob = new Blob([wbout], { type: "application/octet-stream" });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `${tableName}.xlsx`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
};
